/reset.scss';\n@import 'common/common.scss';\n\n\n\n\n.show-menu {\n  transform: translateX($gap*40);\n}\n\n@import 'topic.scss';\n@import 'user.scss';\n@import 'detail.scss';\n\n\n.page-box.page {\n  padding-bottom: 100px;\n  background-color: #fff;\n  transition: all .3s ease;\n  overflow-x: hidden;\n}\n\n\n@import 'iconfont/iconfont.scss';\n@import 'github-markdown.scss';\n","$color31:#313131;\n$colorf0:#f0f0f0;\n$colorfff:#fff;\n$colore7:#e7e7e7;\n$colorfa:#fafafa;\n$color62:#626262;\n$colord4:#d4d4d4;\n$colora0:#a0a0a0;\n$red:#ff5a5f;\n$yellow:#feb501;\n$color80:#80bd01;\n$gap:5PX;\n\n\n/* é¢è² */\n\n$white: #ffffff;\n$light: #d5dbdb;\n$title: #2c3e50;\n$text: #34495e;\n\n/* åºç¡å¸å± */\n\n$padding: 15PX;\n$border: 1PX solid $light;\n$radius: 4PX;\n$font-title: 16PX;\n$font-content: 14PX;\n$font-info: 12PX;\n$font-tag: 10PX;\n\n.mt5 {\n  margin-top: $gap;\n}\n\n.mt10 {\n  margin-top: $gap*2;\n}\n\n.color80 {\n  color: #42b983;\n}\n\n.clear {\n  zoom: 1;\n\n  &:after {\n    content: '';\n    display: table;\n    clear: both;\n  }\n}\n\n\n\n.display-flex {\n  display: -moz-box;\n  /* Firefox */\n  display: -ms-flexbox;\n  /* IE10 */\n  display: -webkit-box;\n  /* Safari */\n  display: -webkit-flex;\n  /* Chrome, WebKit */\n  display: flexbox;\n  display: flex;\n}\n\n.break {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n\n.user {\n  width: 100%;\n  margin: $gap*2 0;\n  padding: 0 $gap*2;\n  display: flex;\n\n  .tab {\n    display: inline-block;\n    width: $gap*8;\n    color: $color62;\n    border-radius: $gap;\n    background-color: $colore7;\n    margin-right: $gap*2;\n    text-align: center;\n    height: $gap*4;\n    line-height: $gap*4;\n    vertical-align: middle;\n  }\n\n  .good {\n    background-color: $color80;\n    color: $colorfff;\n  }\n\n  .title {\n    font-size: $gap*3.6;\n    font-weight: bold;\n    display: block;\n    width: 100%;\n    flex: 1;\n    @extend .break;\n  }\n\n  .head {\n    display: inline-block;\n    width: $gap*9;\n    height: $gap*9;\n    margin-right: $gap*2;\n    border-radius: 10px;\n\n    img {\n      width: $gap*9;\n      border: 2px solid #fff6e6;\n      border-radius: 50%;\n    }\n  }\n\n  .info {\n    overflow: hidden;\n    display: block;\n    width: 100%;\n    flex: 1;\n\n    &:after {\n      clear: both;\n    }\n\n    .t-title {\n      font-size: $gap*3.6;\n      font-weight: bold;\n      width: 100%;\n      color: #333;\n      @extend .break;\n    }\n\n    .mt12 {\n      margin-top: 12px;\n    }\n\n    .cl {\n      display: inline-block;\n      width: 68%;\n\n      .name {\n        color: $color62;\n      }\n\n      .mt10 {\n        margin-top: $gap*2;\n      }\n    }\n\n    .cr {\n      width: 30%;\n      display: inline-block;\n      text-align: right;\n\n      .name {\n        margin-top: $gap*2;\n        color: $color80;\n        font-size: $gap*2.4;\n      }\n    }\n  }\n}\n\n.reply_content {\n  padding: 0 $gap*3;\n  margin-bottom: $gap*3;\n\n  img {\n    width: auto\\9;\n    height: auto;\n    max-width: 100%;\n    vertical-align: middle;\n    border: 0;\n    -ms-interpolation-mode: bicubic;\n  }\n\n  p {\n    line-height: 1.3;\n  }\n}\n\n.no-data {\n  width: 100%;\n  height: 100%;\n  padding: 40% 0;\n  color: $colord4;\n  display: inline-block;\n  font-size: 18PX;\n  text-align: center;\n}\n\n\n/*\n**   rework åççº¿\n**   ------------\n*/\n\n\n\n\n/* ç»¼åå¸å± */\n\n.text-overflow {\n  white-space: nowrap;\n  text-overflow: ellipsis;\n  overflow: hidden;\n}\n\n.title-nooverflow {\n  color: $title;\n  font-size: $font-title;\n  line-height: 1.5;\n}\n\n.title {\n  color: $title;\n  font-size: $font-title;\n  line-height: 1.5;\n  @extend .text-overflow;\n}\n\n.markdown-text {\n  font-size: 28px;\n}\n\n\n\n.tag {\n  padding: 5px 6px;\n  font-size: $font-tag;\n  font-weight: 400;\n  border-radius: $radius;\n  background-color: $colore7;\n  text-align: center;\n  vertical-align: middle;\n}\n\n.user-avatar {\n  display: block;\n  width